<template>
	<div class="article-item" v-bind:class="{ 'is-single': isSingle }">
		<router-link :to="{ name: 'article', params: { id: post.number }}">
			<div class="article-title">
				<div class="title-content" v-html="post.title"></div>
				<div class="time-content" v-html="post.created_at"></div>
			</div>
			<div class="article-body" v-html="post.body"></div>
		</router-link>
		<!--<div v-if="isSingle" class="article-comment">
			<a :href=post.html_url>请到原文issue下评论，点击进入</a>
		</div>-->
	</div>
</template>

<script>
export default {
	name: 'ArticleListItem',
	props:['post','isSingle']
}
</script>

<style lang="less">
.is-single{
	&:hover{
		box-shadow:inset 0 0 0 2px #ccc!important;
	}
	box-shadow:inset 0 0 0 2px #ccc;
	.article-body{
		&>pre{
			margin:10px 2px 10px 0;
			border-left: 2px solid #565656;
			&>code{
				padding:20px 30px;
				background:#e9e9e9!important;
			}
		}
	}
}
.article-item{
	margin:15px 0 10px 0;
	max-width:650px;
	padding:0;
	width:100%;
	transition:all 0.5s;
	&:after{
		content: '';
		margin-top:-2px;
		width: 10%;
		height: 2px;
		left: 45%;
		position: absolute;
		background-color:#dadada;
		transition:all 0.3s;
	}
	&:hover{
		//transform:translate(0,-2px);
		box-shadow:inset 0 0 0 2px #cccccc;
		&:after{
			width:0%;
			left:50%;
		}
	}
	&:active{
		box-shadow:inset 0 0 0 2px #b2b2b2;
	}
	&:last-child{
		&:after{
			visibility: hidden;
		}
	}
	.article-title{
		margin: 30px 30px 25px;
		display: flex;
		justify-content: space-between;
		
		.title-content{
			color:#000;
			font-size:1.3rem;
			border-bottom:1px dashed #999;
			padding-bottom:2px;
		}
		.time-content{
			margin-top:5px;
			flex-shrink:0;
			color:#666;
			font-size:0.8rem;
		}
		
		
	}
	.article-body{
		padding:0;
		color:#555;
		margin:0 0 25px;
		width:100%;
		font-size:0.9rem;
		&>*{
			margin-left:30px;
			margin-right:30px;
		}
		&>pre{
			&>code{
				background:#e8e8e8!important;
			}
		}
		& *{
			max-width:100%;
		}
	}
	.article-comment{
		margin: 20px 2px 0 0;
		padding: 20px 30px;
		color: #555;
		border-left: 2px solid #565656;
		background-color: #eaeaea;
		font-size: 0.9rem;
		&>*{
			max-width:100%;
		}
		a{
			color:#007dbb;
		}
	}
}
@media screen and (max-width: 600px){
	.wzheader{
		padding:0 30px;
	}
	.wzheader .title{
		padding:6px 15px;
		font-size:1.3rem;
	}
	.article-item{
		box-shadow:none;
	}
	.is-single .article-body > pre{
		margin:0;
		border:none;
	}
	.article-item .article-body > pre{
		margin:0;
		code{
			padding:20px 30px;
		}
	}
}

</style>


